body {
    align-content: center;
    align-items: center;
    background-color: #F9F9F9;
    display: flex;
    font-family: 'Roboto', sans-serif;
    justify-content: center;
    margin: 0;
    min-height: 100vh;
    min-height: -webkit-fill-available;
    padding: 0;
}

html {
    height: -webkit-fill-available;
}

::-webkit-scrollbar {
    display: none;
}

.container-empresas-card {
    align-content: space-evenly;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 3em 0;
    width: 65vw;
}

.container-empresas-card-hidden { display: none; }

.empresa-card {
    background: linear-gradient(180deg, #61C0E9 0%, #52B4DE 22.92%, #FFFFFF 22.93%, #FFFFFF 100%);
    border-radius: 1rem;
    box-shadow: 0px 1px 10px 3px rgba(0, 0, 0, 0.4);
    display: flex;
    flex-direction: column;
    height: 310px;
    margin: 0 .5rem 1rem 0;
    width: 219px;
}

.empresa-card:hover {
    box-shadow: 0px 1px 15px 3px rgba(0, 0, 0, 0.6);
    cursor: pointer;
    height: 320px;
    transition: .3s;
}

.header-card {
    display: flex;
    margin: 0;
    width: 100%;
}

.icon-empresa {
    background: #E8E8E8;
    border: 3px solid #FFFFFF;
    border-radius: 100px;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    height: 65px;
    margin-left: 1rem;
    margin-top: 1.5rem;
    padding: .1rem;
    width: 65px;
}

.header-card h1 {
    align-items: center;
    color: #FFFFFF;
    display: flex;
    font-weight: 700;
    font-size: 13px;
    height: 3.1rem;
    justify-content: center;
    letter-spacing: .04rem;
    line-height: 16px;
    margin: 0;
    overflow: scroll;
    padding: .5rem 0;
    right: 0;
    text-align: center;
    top: 0;
    width: 8.5rem;
}

.title-card {
    display: flex;
    flex-direction: row-reverse;
    margin-top: -1.8rem;
}

.title-card h2 {
    font-weight: 900;
    font-size: 13px;
    letter-spacing: .05rem;
    line-height: 16px;
    color: #5B5B5B;
    right: 0;
    padding: 0 1rem;
}

.body-card {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: .7rem;
    padding: 0 .8rem;
}

.body-card p {
    align-items: center;
    display: flex;
    flex-direction: row;
    font-size: 15px;
    font-weight: 500;
    height: 3rem;
    line-height: 16px;
    margin: 0 0 1.1rem 0;
    overflow: scroll;
    width: 100%;
}

.body-card .button-card {
    align-content: center;
    align-items: center;
    background: #61C0E9;
    border: none;
    border-radius: 10rem;
    display: flex;
    height: 3rem;
    justify-content: center;
    margin: -.5rem 0 0 0;
    padding: 0;
    width: 7.5rem;
}

.container-loading {
    align-items: center;
    display: flex;
    height: 100vh;
    justify-content: center;
    width: 100vw;
}

.container-loading .loading {
    color: #0191CE;
}